/* eslint-disable jsx-a11y/iframe-has-title */
import React from 'react';
import { sysConfig } from 'yc';
import styles from './index.less'

export default class CemSum extends React.PureComponent {
  constructor(props) {
    super(props);
    this.projectMapConfig = sysConfig.getCfgByKey('cenSum');
    this.state = {
      btns: {}
    }
  }

  componentWillMount() {
    const censum = this.projectMapConfig;
    this.setState({ btns: censum[0].url })
  }

  changeBtn = (e) => {
    console.log(e);
  }
  render() {
    return (
      <div style={{ width: '100%', height: '100%' }}>
        <div>
          {
            this.projectMapConfig.map(x => {
              return (
                <div className={styles.btn} onClick={(e) => this.changeBtn(e)} value>{x.name}</div>
              )
            })
          }
        </div>
        <div style={{ width: '100%', height: '100%' }}>
          <iframe src={this.state.btns} width="100%" height="100%" frameborder={0} />
        </div>
      </div>
    );
  }
}
